<template>
  <div class="bg">
    <header>
      <a href="/" class="logo">Software Engineering</a>
      <div class="nav-logout">
        <a @click="onLogout" id="logout">Log out</a>
      </div>
    </header>

    <section>
      <img src="../assets/curve.png" alt="curve" class="wave" />

      <div class="contentBx">

        <h2 style="margin-bottom: 0.5em;">Hi, {{ user.nickName }}!</h2>

        <div v-if="isSpin">
          <b-spinner medium type="grow" variant="light"></b-spinner>
        </div>

        <div v-else>
          <h3
            class="text-break"
            id="hint--word"
            v-for="(item, index) in hint"
            :key="index"
          >คำใบ้ {{index+1}} > {{item}}</h3>
        </div>

        <a @click="toggle()" id="button" style="cursor: pointer;" v-if="!user.random">
          <span>Random Now!</span>
        </a>
        <div class="rainbow"></div>
      </div>
      <div class="imgBx">
        <img src="../assets/minion.png" alt="mario" />
      </div>
    </section>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "Dashboard",
  props: ["user", "hint"],
  data() {
    return {
      show: false,
      isSpin: false,
    };
  },
  methods: {
    toggle(e = "rnd") {
      this.show = !this.show;
      if (e === "rnd") return this.onRandom();
    },
    onLogout() {
      this.$store.dispatch("logout");
    },
    onRandom() {
      this.isSpin = true;
      this.user.random = true;
      this.$store.dispatch("onRandom", {
        s_id: this.user.s_id,
        permission: this.user.permission,
      });
      this.$emit("loadHint");
    },
  },
  computed: {
    getName() {
      return this.user.firstName.replace(/นาย|นางสาว|นาง/g, () => "");
    },
  },
  watch: {
    hint(a, b) {
      if (a) {
        this.isSpin = false;
      }
    },
  },
};
</script>

<style scoped>

@import '../styles/junior.css';

</style>